.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.server {
  background-color: yellow
}

table tr.spacer td {
  padding-top:2em;
}

.score {
  float: left;
  width:20%;
  outline: black 1px solid
}

.scorebox {
  width:60%;
  margin: 0 20% 0 20%;
  display: inline-block;
}

.court{
  width:60%;
  margin: 0 20% 0 20%;
  background-color: lightgreen
}

.side {
  width:100%;
  float: left;
}

.sideTitle {
  width:100%;
  outline: black 1px solid;
  font-weight: bold;
  height: 3em;
  vertical-align: middle;
  line-height: 3em;
}

.server {
  background-color: yellow;
}

.player {
  width:50%;
  float: left;
  height: 5em;
  font-size: x-large;
  vertical-align: middle;
  line-height: 5em;
}

.scorebox div {
  padding: .5em 0 .5em 0
}

form {
  padding-top: 30px;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



